/* Global Styles */
body {
  font-family: "Poppins", sans-serif;
  background: #f8f9fa;
  color: #333;
  text-align: center;
  padding: 20px;
}

/* Main Container */
.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 40px; /* Space between sections */
}

/* QR Generator Section */
.qr-generator {
  background: white;
  padding: 30px;
  border-radius: 12px;
  box-shadow: 0px 5px 20px rgba(0, 0, 0, 0.1);
  max-width: 500px;
  width: 100%;
}

/* Headings */
h2 {
  font-weight: 700;
  font-size: 24px;
  margin-bottom: 20px;
}

/* Form Layout */
.form-container {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

/* Form Group */
.form-group {
  display: flex;
  flex-direction: column;
  text-align: left;
  gap: 5px;
}

/* Labels */
.form-group label {
  font-weight: bold;
}

/* Input Fields */
.form-group input,
.form-group select {
  padding: 12px;
  border: 1px solid #ddd;
  border-radius: 8px;
  outline: none;
  font-size: 16px;
  width: 90%;
}

/* Color Picker */
.color-picker {
  display: flex;
  align-items: center;
  gap: 10px;
}

.color-picker input {
  width: 60px;
  height: 60px;
  border: none;
  background: none;
}

/* QR Code Preview */
.qr-preview {
  margin: 20px 0;
  padding: 10px;
  background: #fafafa;
  border-radius: 12px;
  box-shadow: 0px 3px 15px rgba(0, 0, 0, 0.1);
  display: flex;
  justify-content: center;
  align-items: center;
  height: 320px;
}

/* Download Button */
.download-btn {
  background: #007bff;
  color: white;
  border: none;
  padding: 12px 18px;
  font-size: 18px;
  font-weight: bold;
  border-radius: 10px;
  cursor: pointer;
  width: 100%;
  transition: 0.3s;
}

.download-btn:hover {
  background: #0056b3;
  transform: scale(1.05);
}

/* Responsive Design */
@media (max-width: 480px) {
  .qr-generator {
      max-width: 90%;
      padding: 20px;
  }
}

